import React from "react";
import styled from "styled-components";
import { useTranslation } from "react-i18next";

//  -   !   ! 2023 
function History() {
  const { t } = useTranslation();
  const data = [
    {
      year: "2023",
      contents: [
        t(" "),
        t("  "),
        t("        "),
        t("   "),
        t("   "),
      ],
    },
    {
      year: "2022",
      contents: [
        t("  "),
        t("    "),
        t("      "),
        t(" SOC     "),
      ],
    },
    {
      year: "2021",
      contents: [
        t("  R&D  "),
        t("   "),
        t(" (JS-02P)"),
        t(" (JS-06)"),
        t("      "),
      ],
    },
    {
      year: "2020",
      contents: [
        t(" ` `"),
        t(" TP `   `"),
        t("     "),
        t("  "),
      ],
    },
    {
      year: "2019",
      contents: [
        t("       "),
        t("  "),
        t("  ()"),
      ],
    },
    {
      year: "2018",
      contents: [
        t("SW  "),
        t("   "),
        t("   "),
        t("()  "),
      ],
    },
    {
      year: "2017",
      contents: [
        t("  "),
        t("  "),
        t("     "),
        t("  "),
      ],
    },
    {
      year: "2016",
      contents: [t("   "), t(" ")],
    },
  ];

  return (
    <HisContainer>
      <Container>
        <Title1st>HISTORY</Title1st>
        <Title2nd>A.I. {t("  ")}</Title2nd>
        {data.map((item, index) => (
          <YearItem key={index}>
            <Year>{item.year}</Year>
            <Divider />
            <Contents>
              {item.contents.map((content, idx) => (
                <ContentItem key={idx}>{content}</ContentItem>
              ))}
            </Contents>
          </YearItem>
        ))}
      </Container>
    </HisContainer>
  );
}

export default History;

// Title   

const Title1st = styled.p`
  font-family: "Pretendard-Bold";
  font-size: 35px;
  color: #0072ff;
  margin-bottom: -7px;

  @media all and (min-width: 768px) and (max-width: 1199px) {
    font-size: 28px;
  }

  @media all and (max-width: 767px) {
    font-size: 21px;
  }
`;

const Title2nd = styled.p`
  font-family: "Pretendard-Bold";
  font-size: 25px;
  margin-bottom: 80px;

  @media all and (min-width: 768px) and (max-width: 1199px) {
    font-size: 28px;
    margin-bottom: 50px;
  }

  @media all and (max-width: 767px) {
    font-size: 21px;
    margin-bottom: 30px;
  }
`;

const HisContainer = styled.div`
  flex: 1 1 0%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgb(255, 255, 255);
`;

const Container = styled.div`
  display: flex;
  padding: 30px;
  flex-direction: column;
`;

const YearItem = styled.div`
  display: flex;
  flex-direction: row;
  margin-bottom: 30px;
  align-items: flex-center;
  justify-content: flex-center;

  @media all and (min-width: 768px) and (max-width: 1199px) {
    font-size: 28px;
  }

  @media all and (max-width: 767px) {
    font-size: 21px;
  }
`;

const Year = styled.div`
  font-family: "Pretendard-Bold";
  font-size: 35px;
  margin-right: 30px;
  width: 60px;
  text-align: right;

  @media all and (min-width: 768px) and (max-width: 1199px) {
    font-size: 28px;
  }

  @media all and (max-width: 767px) {
    font-size: 21px;
  }
`;

const Divider = styled.div`
  height: 30px;
  width: 2px;
  background-color: #000;
  margin: 12px;

  @media all and (min-width: 768px) and (max-width: 1199px) {
    font-size: 28px;
  }

  @media all and (max-width: 767px) {
    font-size: 19px;
  }
`;

const Contents = styled.div`
  font-family: "Pretendard-Bold";
  font-size: 20px;
  display: flex;
  flex-direction: column;
  padding-top: 10px;

  @media all and (min-width: 768px) and (max-width: 1199px) {
    font-size: 28px;
  }

  @media all and (max-width: 767px) {
    font-size: 19px;
  }
`;

const ContentItem = styled.div`
  margin-bottom: 12px;

  @media all and (min-width: 768px) and (max-width: 1199px) {
    font-size: 28px;
  }

  @media all and (max-width: 767px) {
    font-size: 19px;
  }
`;
